// ***********************
// Media Query mixin
// ***********************


$tablet-width: 768px;
$desktop-width: 1024px;

@mixin large-phone {
  @media (min-width: 425px) {
    @content;
  }
} 

@mixin tablet {
  @media (min-width: $tablet-width)  {
    @content; 
  }
}

@mixin desktop {
  @media (min-width: $desktop-width) {
    @content;
  }
}

@mixin tablet__landscape {
  @media (min-device-width: $tablet-width)
  and (max-device-width: $desktop-width)
  and (orientation: landscape) {
    @content;

  }
}

  @mixin desktop__large {
    @media (min-width: 1600px){
      @content;
    }
  }



